import { createContext, useState } from 'react';
import './App.css';
import IdForm from './IdForm';
import IDcardprint from './IDcardprint';
import IdCardPrintPage from './IdCardPrintPage';


export const NidInfo = createContext()


function App() {

  const [formData, setFormData] = useState({})

console.log(formData)

  
  const [isPrint, setPrint] = useState(false)



  return (
    <NidInfo.Provider value={{formData, setFormData , setPrint}}>
      <div className='p-10'>
        
     
     {
      isPrint? <IdCardPrintPage/> : <IdForm setPrint  />
     }
     
         </div>
    </NidInfo.Provider>
  );
}

export default App;