import moment from 'moment';
import React, { useContext, useState } from 'react';
import Button from 'react-bootstrap/Button';
import Col from 'react-bootstrap/Col';
import Form from 'react-bootstrap/Form';
import Row from 'react-bootstrap/Row';
import { NidInfo } from './App';


const IdForm =()=> {

  const {formData, setFormData, setPrint} = useContext(NidInfo)

  const [formInfo, setFormInfo] = useState({})

  const [validated, setValidated] = useState(false);

  const [photoUrl, setphotoUrl] = useState(null);
  const [signUrl, setSignUrl] = useState(null);
  const [qrCode, setQrCode] = useState(null)

  


  function uploadPhoto(event) {
    const imageFile = event.target.files[0];
    const imageUrl = URL.createObjectURL(imageFile);
    setphotoUrl(imageUrl)
  }

  function uploadSign(event) {
    const imageFile = event.target.files[0];
    const imageUrl = URL.createObjectURL(imageFile);
    setSignUrl(imageUrl)
  }

  function uploadQr(event) {
    const imageFile = event.target.files[0];
    const imageUrl = URL.createObjectURL(imageFile);
    setQrCode(imageUrl)
  }


  const setField =(field, value)=>{

    setFormInfo({
      ...formInfo,
      [field]:value
    })
  }


const totayDate = moment().format("D/M/Y"); 


  const handleSubmit = (event) => {
    event.preventDefault();
    const form = event.currentTarget;
    if (form.checkValidity() === false) {
     
      event.stopPropagation();
    }
    setValidated(true);


    const finalData ={
      nameBn:formInfo.nameBn,
      nameEn:formInfo.nameEn,
      father:formInfo.father,
      mother: formInfo.mother,
      dob: formInfo.dob,
      nid: formInfo.nid,
      pin: formInfo.pin,
      birthPlace: formInfo.birthPlace,
      blood:formInfo.blood,
      address: formInfo.address,
      dDate: formInfo.dDate || totayDate,
      photoUrl,
      signUrl,
      qrCode
    }

    setPrint(true)
    setFormData(finalData)

   

  };

  return (
    <div className='bg-green-200 rounded-lg'>
      <div>
        <p className='text-2xl font-bold text-center text-green-900 bg-green-200 pt-4 rounded-lg '> *মেনুয়্যালি আইডি তৈরির ফরম*</p>
      </div>
      <div  className="p-20 bg-gray-400 rounded-lg "> 
      <Form noValidate validated={validated} onSubmit={handleSubmit}>
      <Row className="mb-3">
        <Form.Group as={Col} md="4" controlId="validationCustom01">
          <Form.Label>বাংলা নাম</Form.Label>
          <Form.Control
            required
            type="text"
            value={formData.nameBn}
            placeholder="নাম বাংলা"
            onChange={(e)=> setField('nameBn', e.target.value)}
          />
        </Form.Group>


        <Form.Group as={Col} md="4" controlId="validationCustom02">
          <Form.Label>ইংরেজি নাম</Form.Label>
          <Form.Control
            required
            type="text"
            value={formData.nameEn}
            placeholder="ইংরেজি নাম"
            onChange={(e)=> setField('nameEn', e.target.value)}
          />
        </Form.Group>


        <Form.Group as={Col} md="4" controlId="validationCustom03">
          <Form.Label> জন্ম তারিখ</Form.Label>
            <Form.Control
              type="text"
              value={formData.dob}
              placeholder="জন্ম তারিখ 01 Apr 1997"
              onChange={(e)=> setField('dob', e.target.value)}
              required
            />
            
        </Form.Group>
      </Row>


      <Row className="mb-3">
        <Form.Group as={Col} md="4" controlId="validationCustom04">
          <Form.Label>পিতার নাম বাংলা</Form.Label>
          <Form.Control
              type="text"
              value={formData.father}
              placeholder="পিতার নাম বাংলা"
              onChange={(e)=> setField('father', e.target.value)}
              required
            />
        
        </Form.Group>



        <Form.Group as={Col} md="4" controlId="validationCustom05">
        <Form.Label>মাতার নাম বাংলা</Form.Label>
          <Form.Control
              type="text"
              value={formData.mother}
              placeholder="মাতার নাম বাংলা"
              onChange={(e)=> setField('mother', e.target.value)}
              required
            />
        
        </Form.Group>



        <Form.Group as={Col} md="4" controlId="validationCustom06">
          <Form.Label> আইডি নাম্বার </Form.Label>
          <Form.Control
              type="text"
              value={formData.nid}
              placeholder="আইডি নাম্বার"
              onChange={(e)=> setField('nid', e.target.value)}
              required
            />
          
        </Form.Group>
      </Row>

      <Row className="mb-3">
        <Form.Group as={Col} md="6" controlId="validationCustom07">
          <Form.Label>পিন নাম্বার</Form.Label>
          <Form.Control
              type="text"
              value={formData.pin}
              placeholder="পিন নাম্বার ১৭ ডিজিট"
              onChange={(e)=> setField('pin', e.target.value)}
              required
            />
        
        </Form.Group>



        <Form.Group as={Col} md="4" controlId="validationCustom08">
        <Form.Label>জন্মস্থান</Form.Label>
          <Form.Control
              type="text"
              value={formData.birthPlace}
              placeholder="জন্মস্থান জেলা"
              onChange={(e)=> setField('birthPlace', e.target.value)}
              required
            />
        
        </Form.Group>



        <Form.Group as={Col} md="2" controlId="validationCustom09">
          <Form.Label> রক্তের গ্রুপ </Form.Label>
          <Form.Control
              type="text"
              value={formData.blood}
              placeholder="রক্তের গ্রুপ যদি থাকে"
              onChange={(e)=> setField('blood', e.target.value)}
              
            />
          
        </Form.Group>
      </Row>


      <Row className="mb-3">
        <Form.Group as={Col} md="9" controlId="validationCustom10">
        <Form.Label> ঠিকানা </Form.Label>
          <Form.Control
              type="text"
              value={formData.address}
              placeholder="ঠিকানা"
              onChange={(e)=> setField('address', e.target.value)}
              defaultValue='বাসা/হোল্ডিং: , গ্রাম/রাস্তা: , ডাকঘর: '
              required
            />
        
        </Form.Group>



        <Form.Group as={Col} md="3" controlId="validationCustom11">
        <Form.Label> প্রদানের তারিখ</Form.Label>
          <Form.Control
              type="text"
              value={formData.dDate}
              placeholder="প্রদানের তারিখ ০১/০৫/২০২৩"
              onChange={(e)=> setField('dDate', e.target.value)}
              defaultValue={totayDate}
             
              required
            />
        
        </Form.Group>



    
      </Row>


<Row>
<div class="mb-3" style={{ display: 'flex' }}>
                    <label for="photo" class="form-label">ছবি আপলোড করুন</label>
                    <input type="file" class="form-control-file" accept="image/*" name="photoupload" onChange={uploadPhoto} required />
                </div>
                <div class="mb-3" style={{ display: 'flex' }}>
                    <label for="photo" class="form-label">সাইন আপলোড</label>
                    <input type="file" class="form-control-file" accept="image/*" name="signupload" onChange={uploadSign} required />
                </div>


                <div class="mb-3" style={{ display: 'flex' }}>
                    <label for="photo" class="form-label">কোড আপলোড</label>
                    <input type="file" class="form-control-file" accept="image/*" name="qrUpload" onChange={uploadQr} required />
                </div>
</Row>


      <Form.Group className="mb-3">
        <Form.Check
          required
          label="উপরের সব তথ্য ঠিক আছে"
          feedback="আপনাকে ঠিক দিতে হবে."
          feedbackType="invalid"
        />
      </Form.Group>
      <Button type="submit">তৈরি করুন</Button>
    </Form>
      </div>
    </div>
  );
}

export default IdForm